/* .user-header {
} */

.user-banner {
  float: left;
}

.user-pfp-follow {
  float: left;
  width: 600px;
}

.user-pfp-follow div {
  float: left;
}

.user-photo {
  margin-top: -16%;
  margin-left: 20px;
}

.user-photo img {
  border-radius: 50%;
  border: white solid 4px;
}

.user-follow-button {
  width: 432px;
  /* 600 - pfp.leftmargin - pfp.border - pfp.width */
}

.user-follow-button button {
  float: right;
  width: 120px;
  height: 40px;
  font-size: 12px;
  margin: 10px;
}

.user-info {
  width: 600px;
  float: left;
  margin-bottom: 20px;
}

.user-info div {
  float: left;
}

.user-displayname,
.user-handle {
  text-align: left;
  margin-left: 20px;
  width: 440px;
}

#user-joindate {
  width: 120px;
  margin: 5px 15px 5px 5px;
  float: right;
  font-size: 12px;
  text-align: right;
  font-style: italic;
}

.user-displayname {
  font-weight: 900;
  font-size: 22px;
}

.user-handle {
  color: grey;
  font-size: 16px;
}

.user-handle::before {
  content: "@";
}

.user-bio {
  width: 540px;
  margin: 10px 30px;
  text-align: left;
  font-size: 14px;
}

.user-tabs div {
  float: left;
  font-size: 14px;
  margin-left: 25px;
}

.user-current-tab {
  text-decoration: underline;
}
